:root {
  // MAIN_COLOR
  --swiper-theme-color: #94343de8 !important;

  // NAVIGATION
  --swiper-navigation-size: 3.3rem !important;
  --swiper-navigation-top-offset: 50%;
  --swiper-navigation-sides-offset: 2rem;
  --swiper-navigation-color: var(--swiper-theme-color);
  @include respond(bp-small) {
    --swiper-navigation-size: 4.3rem !important;
  }

  // PAGINATION
  --swiper-pagination-color: var(--swiper-theme-color);
  --swiper-pagination-left: auto;
  --swiper-pagination-right: 0.8rem;
  --swiper-pagination-bottom: 0.8rem;
  --swiper-pagination-top: auto;
  --swiper-pagination-fraction-color: inherit;
  --swiper-pagination-progressbar-bg-color: rgba(192, 67, 67, 0.37);
  --swiper-pagination-progressbar-size: 0.4rem;
  --swiper-pagination-bullet-size: 0.8rem;
  --swiper-pagination-bullet-width: 0.8rem;
  --swiper-pagination-bullet-height: 0.8rem;
  --swiper-pagination-bullet-inactive-color: #8b0606 !important;
  --swiper-pagination-bullet-inactive-opacity: 0.2;
  --swiper-pagination-bullet-opacity: 1;
  --swiper-pagination-bullet-horizontal-gap: 0.4rem;
  --swiper-pagination-bullet-vertical-gap: 0.6rem;
  @include respond(bp-small) {
    --swiper-pagination-bullet-width: 1.1rem;
    --swiper-pagination-bullet-height: 1.1rem;
  }
}

.swiper {
  max-width: 117rem;
  height: 64rem;

  @include respond(bp-desktop-abnormal) {
    height: 55vh;
    max-height: 60rem;
  }

  @include respond(bp-desktop-mid) {
    height: 70vh;
    max-height: 65rem;
  }

  @include respond(bp-desktop-large) {
    max-width: 100%;
    max-height: 70rem;
    height: 75rem;
  }

  @include respond(bp-large) {
    max-width: 100%;
    height: 65rem;
  }

  @include respond(bp-medium) {
    height: 75vh;
  }

  @include respond(bp-small) {
    height: 70vh;
  }
}

.swiper-container {
  @include respond(bp-small) {
    padding: 0;
  }
}

.swiper-slide {
  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover !important;
  }
}
