.dropdown-btn {
  z-index: 100000;
  width: 5rem;
  height: 5rem;
  cursor: pointer;
  background: none;
  border: 0;
  transition: all .3s cubic-bezier(.06, .25, .37, .94);
  display: none;
  position: relative;
}

.dropdown-btn:hover {
  transform: translateY(-1px);
}

.dropdown-btn:active {
  transform: translateY(0)scale(.95);
}

.dropdown-btn:after {
  width: 20px;
  height: 2px;
  content: "";
  background-color: #d7c5c4;
  transition: all .3s ease-in;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 62.5em) {
  .dropdown-btn {
    display: block;
  }
}

.dropdown-btn span:before, .dropdown-btn span:after {
  width: 20px;
  height: 2px;
  background-color: #d7c5c4;
  transition: all .3s ease-in;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.dropdown-btn span:after, .dropdown-btn span:before {
  content: "";
}

.dropdown-btn span:after {
  transform: translate(-50%, 250%);
}

.dropdown-btn span:before {
  transform: translate(-50%, -300%);
}

.dropdown-btn--active span:before {
  transform: translate(-50%, -50%)rotate(-45deg);
}

.dropdown-btn--active span:after {
  transform: translate(-50%, -50%)rotate(45deg);
}

.dropdown-btn--active:after {
  opacity: 0;
  transform: translate(100%, -50%);
}

.dropdown-btn--active {
  transform: scale(.9);
}

.dropdown-btn--active:hover {
  transform: translateY(-1px)scale(.88);
}

.dropdown-btn--active:active {
  transform: translateY(0)scale(.86);
}

